import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileFtpDownListResponse } from "../../Model/Response/FileFtpDownListResponse";
import { FileFtpDownResponse } from "../../Model/Response/FileFtpDownResponse";
import { FileFtpDownViewModel } from '../../Model/ViewModel/FileFtpDownViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileFtpDownServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileFtpDownServicesProvider extends FileServicesProvider {
  private FileFtpDowns: FileFtpDownListResponse;
  private FileFtpDown: FileFtpDownResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileFtpDown";
    console.log("Hello FileFtpDownServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileFtpDown?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileFtpDown/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileFtpDowns = new FileFtpDownListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileFtpDowns;
    });
  }



  public PostPutAsync(FileFtpDownViewModel:FileFtpDownViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileFtpDown";
    else
    sub_url = "FileFtpDown/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileFtpDownViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileFtpDownViewModel,mode).map(
      item => {
        this.FileFtpDown = new FileFtpDownResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileFtpDown;
      }
    );
  }
}



