import { FileFtpDownServicesProvider } from "../../providers/file-ftp-down-services/file-ftp-down-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileFtpDownViewModel } from "../../Model/ViewModel/FileFtpDownViewModel";
import { FilePathServicesProvider } from "../../providers/file-path-services/file-path-services";
import { FilePathViewModel } from "../../Model/ViewModel/FilePathViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";
import { FtpRemoteViewModel } from "../../Model/ViewModel/FtpRemoteViewModel";
import { FtpDirectoryBrowserViewModel } from "../../Model/ViewModel/FtpDirectoryBrowserViewModel";

/**
 * Generated class for the FileFtpDownModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-ftp-down-add-edit-modal",
  templateUrl: "file-ftp-down-add-edit-modal.html"
})
export class FileFtpDownAddEditModalPage {
  func_no: number = 1;
  func_key: string = "014|" + this.func_no;
  item: FileFtpDownViewModel;
  default_path: FilePathViewModel[];
  remote_file_path: FilePathViewModel;
  down_file_path: FilePathViewModel;
  remote_bk_file_path: FilePathViewModel;
  mode: string;
  CanEditBatch: boolean;
  CanFileBrowser: boolean;
  CanFtpFileBrowser: boolean;
  title: string;
  FileFtpDown_type: string;
  min_exec_file_seq: number = 10000;
  max_exec_file_seq: number = this.min_exec_file_seq + 9999;
  range_exec_file_seq: string;
  change_mode: string;
  filter: FtpDirectoryBrowserViewModel;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileFtpDownServices: FileFtpDownServicesProvider,
    private FilePathServices: FilePathServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider
  ) {
    this.range_exec_file_seq = String.Format(
      "{0}-{1}",
      this.min_exec_file_seq,
      this.max_exec_file_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new FileFtpDownViewModel();
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_group = navParams.data.item.exec_group;
    this.item.file_keyword = navParams.data.item.file_keyword;
    this.item.file_min_length = navParams.data.item.file_min_length;
    this.item.file_max_length = navParams.data.item.file_max_length;
    this.item.is_active = navParams.data.item.is_active;
    this.item.file_proc_key = navParams.data.item.file_proc_key;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.can_rerun = navParams.data.item.can_rerun;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.note = navParams.data.item.note;
    this.item.file_proc = navParams.data.item.file_proc;
    this.item.empty_file_check_flag = navParams.data.item.empty_file_check_flag;
    this.item.is_fail_stop = navParams.data.item.is_fail_stop;
    this.item.remote_file_path_key = navParams.data.item.remote_file_path_key;
    this.item.remote_bk_file_path_key =
      navParams.data.item.remote_bk_file_path_key;
    this.item.down_file_path_key = navParams.data.item.down_file_path_key;
    this.item.remote_file_path = navParams.data.item.remote_file_path;
    this.item.remote_bk_file_path = navParams.data.item.remote_bk_file_path;
    this.item.down_file_path = navParams.data.item.down_file_path;
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.remoteViewModel=new FtpRemoteViewModel();
    if (navParams.data.item.remoteViewModel != null) {
      this.item.remoteViewModel.exec_file_seq=navParams.data.item.remoteViewModel.exec_file_seq;
      this.item.remoteViewModel.remote_conn_id =
        navParams.data.item.remoteViewModel.remote_conn_id;
      this.item.remoteViewModel.conn_type_key =
        navParams.data.item.remoteViewModel.conn_type_key;
      this.item.remoteViewModel.encrypt_method_key =
        navParams.data.item.remoteViewModel.encrypt_method_key;
        this.item.remoteViewModel.remote_conn_string =
        navParams.data.item.remoteViewModel.remote_conn_string;
      this.item.remoteViewModel.conn_type =
        navParams.data.item.remoteViewModel.conn_type;
      this.item.remoteViewModel.encrypt_method =
        navParams.data.item.remoteViewModel.encrypt_method;
    }

    if (this.change_mode === "n") {
      this.get_default_path();
      this.get_ftp_conn_string("defaultftp");
    }
    if (this.change_mode === "c") {
      this.get_max_exec_file_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;
    this.CanFileBrowser = navParams.data.CanFileBrowser;
    this.CanFtpFileBrowser = navParams.data.CanFtpFileBrowser;

    this.title = "FTP下載";
  }
  openNavPage(parent_path) {
    let page;
    if (parent_path.indexOf("\\") >= 0) page = "DirectoryBrowserPage";
    if (parent_path.indexOf("/") >= 0) page = "FtpDirectoryBrowserPage";
    this.filter=new FtpDirectoryBrowserViewModel();
    this.filter.remote_conn_id = this.item.remoteViewModel.remote_conn_id;
    this.filter.remote_conn_string = this.item.remoteViewModel.remote_conn_string;
    this.filter.conn_type_key = this.item.remoteViewModel.conn_type_key;
    this.filter.conn_type = this.item.remoteViewModel.conn_type;
    this.filter.encrypt_method_key = this.item.remoteViewModel.encrypt_method_key;
    this.filter.encrypt_method = this.item.remoteViewModel.encrypt_method;
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      if (page === "FtpDirectoryBrowserPage")
        this.navCtrl.push(page, {
          parent_path: parent_path,
          parent_page: this,
          is_modal: true,
          filter: this.filter
        });
      else
        this.navCtrl.push(page, {
          parent_path: parent_path,
          parent_page: this,
          is_modal: true
        });
    });
    this.global.dismissLoading();
  }
  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remoteViewModel.remote_conn_id,
      conn_type_key:"065|002",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remoteViewModel.remote_conn_id = select_data.remote_conn_id;
      this.item.remoteViewModel.remote_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  SelectConnType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.remoteViewModel.conn_type_key,
      code_type: "006"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remoteViewModel.conn_type_key = select_data.code_key;
      this.item.remoteViewModel.conn_type = select_data.code_desc;
      if( this.item.remoteViewModel.conn_type_key==="006|3")
      {
        this.item.remoteViewModel.encrypt_method_key="046|2";
        this.item.remoteViewModel.encrypt_method="Explicit_SSL(外顯式)";
      }
      else
      {
        this.item.remoteViewModel.encrypt_method_key="046|1";
        this.item.remoteViewModel.encrypt_method="無";
      }
    });

    modal.present();
  }

  SelectEncryptMethod() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.remoteViewModel.encrypt_method_key,
      code_type: "046"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remoteViewModel.encrypt_method_key = select_data.code_key;
      this.item.remoteViewModel.encrypt_method = select_data.code_desc;
    });

    modal.present();
  }
  SelectFileProcType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.file_proc_key,
      code_type: "018"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.file_proc_key = select_data.code_key;
      this.item.file_proc = select_data.code_desc;
    });

    modal.present();
  }
  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }
  SelectRemoteFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.remote_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_file_path_key = select_data.code_key;
      this.item.remote_file_path = select_data.code_desc;
    });

    modal.present();
  }
  SelectDownFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.down_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.down_file_path_key = select_data.code_key;
      this.item.down_file_path = select_data.code_desc;
    });

    modal.present();
  }
  SelectRemoteBkFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.remote_bk_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_bk_file_path_key = select_data.code_key;
      this.item.remote_bk_file_path = select_data.code_desc;
    });

    modal.present();
  }

  get_max_exec_file_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileFtpDownServices.GetMaxExecFileSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_file_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_default_path() {
    this.global.createLoader("取得預設資料夾中...");
    this.global.loading.present().then(() => {
      this.FilePathServices.GetDefaultListsByAsync(this.func_key).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.default_path = data.Model;
            this.remote_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|RDN"
            )[0];
            this.item.remote_file_path_key = this.remote_file_path.file_path_key;
            this.item.remote_file_path = this.remote_file_path.file_path;

            this.remote_bk_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|RBK"
            )[0];
            this.item.remote_bk_file_path_key = this.remote_bk_file_path.file_path_key;
            this.item.remote_bk_file_path = this.remote_bk_file_path.file_path;

            this.down_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|DN"
            )[0];
            this.item.down_file_path_key = this.down_file_path.file_path_key;
            this.item.down_file_path = this.down_file_path.file_path;

            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileFtpDownServices.GetMaxExecSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_ftp_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.remoteViewModel.remote_conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileFtpDownModalPage");
  }
}



