export class FtpRemoteViewModel {
  /// <summary>
///  轉檔序號
/// <summary>
public exec_file_seq: number;
/// <summary>
///  遠端連線編號
/// <summary>
public remote_conn_id: string;
/// <summary>
///  連線類型主鍵
/// <summary>
public conn_type_key: string;
/// <summary>
///  壓密方法主鍵
/// <summary>
public encrypt_method_key: string;

/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  遠端連線字串
/// <summary>
public remote_conn_string: string;
/// <summary>
///  連線類型
/// <summary>
public conn_type: string;
/// <summary>
///  壓密方法
/// <summary>
public encrypt_method: string;
}



